<template>
 <div class="container">
     <nav class="navbar navbar-expand-lg navbar-dark bg-primary fixed-top" id="mainNav">
      <div class="container">
        <a class="navbar-brand js-scroll-trigger" href="/">
          <img src="../../public/images/logo.jpg" alt="">
        </a>
        <button
          class="navbar-toggler"
          type="button"
          data-toggle="collapse"
          data-target="#navbarResponsive"
          aria-controls="navbarResponsive"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span class="navbar-toggler-icon"></span>
        </button>
        <div class="collapse navbar-collapse" id="navbarResponsive">
          <ul class="navbar-nav ml-auto">
            <li class="nav-item">
              <a data-toggle="modal" data-target="#modelId" href="" class="nav-link text-light">
                Payment dispute
                </a>
            </li>
            <li class="nav-item">
              <a href="https://bit.ly/aida-feedback" target="_blank" class="nav-link text-light">
                Complain/Feedback
                </a>
            </li>
            <!-- <li class="nav-item">
              <a href="/staff-login" class="nav-link text-light">
                FAQ
                </a>
            </li> -->

          </ul>
        </div>
      </div>
    </nav>
    <main class="pt-5 mt-5">
      <div class="row" v-if="psid">
        <div class="col-md-8">
          <form @submit="onSubmit" class="card shadow">
            <div class="card-header text-center">
              MAKE PAYMENT
            </div>
            <div class="card-body">
              <div class="form-group">
                <label for="firstName">First name</label>
                <input type="text" id="firstName" class="form-control"
                  required aria-describedby="helpId" v-model.trim="clientData.customer_firstname">
              </div>

              <div class="form-group">
                <label for="lastName">Last name</label>
                <input type="text" id="lastName" class="form-control"
                  required aria-describedby="helpId" v-model.trim="clientData.customer_lastname">
              </div>

              <div class="form-group">
                <label for="email">Email</label>
                <input type="email" id="email" class="form-control"
                  required aria-describedby="helpId" v-model.trim="clientData.customer_email">
              </div>

              <div class="form-group">
                <label for="phone">Phone number</label>
                <input type="tel" id="phone" class="form-control"
                  required aria-describedby="helpId" v-model.trim="clientData.customer_phone">
              </div>

              <div class="form-group" title="Enter amount you wish to fund your account with">
                <label for="amount">Amount</label>
                <input type="Number" id="amount" class="form-control" min="100" step="1"
                  required aria-describedby="helpId" v-model.number="clientData.amount">
              </div>
            </div>

            <div class="alert alert-danger m-3 alert-dismissible fade show"
              v-if="erroredPayment" role="alert">
              <button type="button" class="close" data-dismiss="alert" aria-label="Close">
                <span aria-hidden="true">&times;</span>
                <span class="sr-only">Close</span>
              </button>
              <strong>ERROR:</strong> {{errorMsg}}
            </div>

            <div class="card-footer">
              <button class="btn btn-success btn-block" v-if="!loading">
                CONTINUE WITH PAYMENT</button>
              <button v-else class="btn btn-success btn-block disabled" disabled>
                <div class="spinner-border text-light mr-3"></div> Loading...</button>
            </div>
          </form>
        </div>
      </div>
      <div class="row" v-else>
        <div class="col-md-8">
          <div class="card">
            <div class="card-body">
              <ul class="list-group">
                <li class="list-group-item">
                  You MUST initiate a chat with <a href="https://www.facebook.com/aida_ng">aida_ng</a> before top-up of your account
                </li>
                <li class="list-group-item">Payment successful, but didnt get balance updated?
                  <a data-toggle="modal" data-target="#modelId" href="">Log a dispute now</a></li>
                <li class="list-group-item">Got a feedback/complain? <a href="https://bit.ly/aida-feedback" target="_blank">click here </a></li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </main>
    <!-- Modal -->
    <div class="modal fade" id="modelId"
      tabindex="-1" role="dialog" aria-labelledby="modelTitleId" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">Payment dispute</h5>
              <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
          </div>
          <div class="modal-body">
            <div class="card">
              <div class="card-body">
                <div class="text-center">
                  Confirm status of payment by providing payment reference code
                </div>
                <div class="form-group">
                  <input type="text" placeholder="Transaction reference"
                    class="form-control" id="ref" v-model="ref" required/>
                </div>
                <button class="btn btn-primary" @click="checkStatus()"
                  v-show="ref.length > 2" >Check status</button>
              </div>

            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
          </div>
        </div>
      </div>
    </div>
 </div>
</template>

<script>
export default {
  name: 'Home',
  data() {
    return {
      psid: false,
      ref: '',
      erroredPayment: false,
      loading: false,
      errorMsg: '',
      msg: '',
      clientData: {
        PBFPubKey: 'FLWPUBK_TEST-8de56613fc1ffdf88d91aea567393551-X',
        customer_firstname: '',
        customer_lastname: '',
        customer_email: '',
        currency: 'NGN',
        txref: `aidapay_${new Date().getTime()}${Math.random().toString(36).slice(2)}${Math.floor(Math.random() * 100)}`,
        customer_phone: '',
        amount: 1000,
        meta: [{
          metaname: 'psid',
          metavalue: '',
        }],
      },
    };
  },
  methods: {
    onSubmit: function(e) { /*eslint-disable-line */
      e.preventDefault();
      this.loading = true;
      this.erroredPayment = false;
      this.errorMsg = '';
      this.pay(this.clientData);

      setTimeout(() => {
        this.loading = false;
      }, 2500);
    },
    pay: async function(clientData){ /*eslint-disable-line */
      // initiate payment
      if (!window.RavePaymentPopUpForAida) {
        this.erroredPayment = true;
        this.errorMsg = 'Unable to load payment gateway. Check your internet conection or manually reload page';
        return;
      }

      const self = this;
      const handler = window.RavePaymentPopUpForAida({
        ...clientData,
        callback: (response) => {
          const txref = response.data.tx.txRef;
          if (response.data.tx.chargeResponseCode === '00' || response.data.tx.chargeResponseCode === '0') {
            // redirect to a success page
            self.$router.push(`/verify/?ref=${txref}`);
          } else {
            // redirect to a failure page.
            self.erroredPayment = true;
            self.errorMsg = `Payment not received. Try again or use this reference ${txref} to check dispute`;
          }
          handler.close(); // use this to close the modal immediately after payment.
        },
      });
    },
    checkStatus: function (){/*eslint-disable-line */
      // Hide the Modal
      window.$('#modelId').modal('hide');
      this.$router.push(`/verify/?ref=${this.ref}`);
    },
  },
  created: function() { /* eslint-disable-line */
    this.psid = this.$route.query.psid || false;
    this.clientData.meta[0].metavalue = (this.psid.length >= 2) ? this.psid : '';
  },
};
</script>
