<template>
 <div class="container">
     <nav class="navbar navbar-expand-lg navbar-dark bg-primary fixed-top" id="mainNav">
      <div class="container">
        <a class="navbar-brand js-scroll-trigger" href="/">
          <img src="../../public/images/logo.jpg" alt="">
        </a>
        <button
          class="navbar-toggler"
          type="button"
          data-toggle="collapse"
          data-target="#navbarResponsive"
          aria-controls="navbarResponsive"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span class="navbar-toggler-icon"></span>
        </button>
        <div class="collapse navbar-collapse" id="navbarResponsive">
          <ul class="navbar-nav ml-auto">
            <!-- <li class="nav-item">
              <a data-toggle="modal" data-target="#modelId" href="" class="nav-link text-light">
                Payment dispute
                </a>
            </li> -->
            <li class="nav-item">
              <a href="https://bit.ly/aida-feedback" target="_blank" class="nav-link text-light">
                Complain/Feedback
                </a>
            </li>
            <!-- <li class="nav-item">
              <a href="/staff-login" class="nav-link text-light">
                FAQ
                </a>
            </li> -->

          </ul>
        </div>
      </div>
    </nav>
    <main class="pt-5 mt-5">
      <div class="row">

        <div class="col-md-12 jumbotron text-center " v-if="loading">
            <div class="spinner-border p-5 jumbotron bg-dark text-light display-1"></div>
            Verifying...
        </div>

        <div class="col-md-8" v-else-if="errored">
          <div class="alert alert-danger" role="alert">
            <strong>ERROR:</strong> {{errorMsg}}
            <button class="btn btn-dark" @click="verifyPayment()">Try again</button>
          </div>
        </div>

        <div class="col-md-8" v-else-if="!errored">
          <div class="alert alert-success" role="alert" >
            <strong>Payment received successfully for reference {{ref}}.</strong>
            <div v-if="msg.payment_date && msg.status">
            <br>
            <span class="badge badge-primary">Date paid:</span> {{msg.payment_date}}
            <br>
            <span class="badge badge-primary">Payment status:</span> {{msg.status}}
            <br>
            <span class="badge badge-primary">Name:</span> {{msg.firstName}}&nbsp;{{msg.lastName}}
            <br>
            <span class="badge badge-primary">Email:</span> {{msg.email}}
            <br>
            <span class="badge badge-primary">Phone:</span> {{msg.phone}}
            <br>
            <span class="badge badge-primary">Amount paid:</span> NGN {{msg.amount}}

            </div>
          </div>
        </div>

        <div class="col-md-8" v-else>
          <div class="alert alert-warning" role="alert" >
            <strong>Oops:</strong>
            Something went wrong. Payment reference {{ref}} could not be verified
            {{errored}}
            <hr>
            <button class="btn btn-dark" @click="verifyPayment()">Try again</button>
          </div>
        </div>

      </div>
    </main>
 </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'Verify',
  data() {
    return {
      ref: false,
      errored: false,
      loading: false,
      errorMsg: '',
      msg: {},
    };
  },
  methods: {
    verifyPayment: async function() { /*eslint-disable-line */
      this.loading = true;
      this.errored = false;
      this.errorMsg = '';
      // admin login
      if (this.ref && this.ref.length >= 2) {
        await axios.get(`http://localhost:8100/flutterwave/verify-payment/${this.ref}`)
          .then((response) => {
            this.errored = (response.data && response.data.errored);
            this.errorMsg = (this.errored) ? response.data.message : `Unable to verify payment with reference of: ${this.ref}`;
            this.msg = (!this.errored) ? response.data.message : {};
          }).catch((err) => {
            this.errored = true;
            this.errorMsg = (err.response && err.response.data.message) ? err.response.data.message : `Unable to verify payment with reference of: ${this.ref} 500_INTERNAL_ERROR`;
          });
      } else {
        this.errored = true;
        this.errorMsg = 'Payment reference is empty or invalid. Unable to verify payment';
      }

      setTimeout(() => {
        this.loading = false;
      }, 3500);
    },
  },
  created: function() { /* eslint-disable-line */
    this.ref = this.$route.query.ref || false;
    this.verifyPayment();
  },
};
</script>
